import { useState } from "react";
import { MainMenu } from "./mainMenu";
import { SocialLinks } from "./socialLinks";
import { SignIn } from "./signIn";
import { SignUp } from "./signUp";
import { SignOut } from "./signOut";
import { Cart } from "./cart";
import { NavLink } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { AlternativeMenu } from "./alternativeMenu";
import { useStoreContext } from "../context/store.context";

export function Header() {
  const {
    signedIn = {},
    handleSignOut,
    setProductsFilter,
    cart,
    handleAmountChange,
    removeFromCart,
    setModalFormType,
  } = useStoreContext();
  const [cartVisible, setCartVisible] = useState(false);
  //const [currentLocation,setCurrentLocation]=useState(useLocation().pathname);
  // let currentLocation = useLocation();
  // console.log(currentLocation.pathname);

  return (
    <header>
      <div id="headerTop">
        <div id="theUser">
          <span>
            <i className="bi bi-person-fill"></i>
          </span>
          <div id="userOptions">
            {!signedIn.id ? (
              <div className="d-inline-block" id="noLoginP">
                <SignIn setModalFormType={setModalFormType} />
                /
                <SignUp setModalFormType={setModalFormType} />
              </div>
            ) : (
              <div className="d-inline-block" id="loginP">
                <SignOut signedIn={signedIn} handleSignOut={handleSignOut} />
              </div>
            )}
          </div>
        </div>
        <div id="adminLinkDiv"></div>
        <SocialLinks />
      </div>
      <div id="headerBottom">
        <div id="logoDiv">
          <NavLink to="/">
            <img id="logo" src="./Imgs/My-Store-Logo2.png" alt="store logo" />
          </NavLink>
        </div>

        {useLocation().pathname === "/" ? (
          <MainMenu setProductsFilter={setProductsFilter} />
        ) : (
          <AlternativeMenu />
        )}
        <div id="theCart">
          <button
            onClick={() => setCartVisible(!cartVisible)}
            className="btn"
            id="myCart"
          >
            <span id="cartProductsNum">{cart.cartProducts?.length || 0}</span>
            <i className="bi bi-cart-plus-fill"></i>
          </button>
          {cartVisible && (
            <Cart
              cart={cart}
              handleAmountChange={handleAmountChange}
              removeFromCart={removeFromCart}
            />
          )}
        </div>
      </div>
    </header>
  );
}
