import httpService from "../services/httpService";
import { useState, useEffect, createContext, useContext } from "react";

const contextMustBeUsedError = () => {
  throw new Error(
    "You must use storeContext Provider for Consumer to work properly"
  );
};

export const storeContext = createContext({
  signedIn: null,
  products: null,
  productsFilter: null,
  setProductsFilter: contextMustBeUsedError,
  cart: null,
  modalFormType: null,
  setModalFormType: contextMustBeUsedError,
  modalFormError: null,
  productToEdit: null,
  setProductToEdit: contextMustBeUsedError,
  handleSignedIn: contextMustBeUsedError,
  handleSignOut: contextMustBeUsedError,
  handleAmountChange: contextMustBeUsedError,
  removeFromCart: contextMustBeUsedError,
  addToCart: contextMustBeUsedError,
  handleNewProduct: contextMustBeUsedError,
  handleEditProduct: contextMustBeUsedError,
  handleDeleteProduct: contextMustBeUsedError,
});
storeContext.displayName = "storeContext";

export function StoreProvider({ children }) {
  const [signedIn, setSignedIn] = useState({});
  const [products, setProducts] = useState([]);
  const [productsFilter, setProductsFilter] = useState("AllProducts");
  const [cart, setCart] = useState({});
  const [modalFormType, setModalFormType] = useState(null);
  const [modalFormError, setModalFormError] = useState("");
  const [productToEdit, setProductToEdit] = useState(null);

  //Get Products
  useEffect(() => {
    getProducts();
    async function getProducts() {
      await httpService
        .get("/products")
        .then((response) => setProducts(response.data));
    }
  }, []);

  //Get Saved User
  useEffect(() => {
    getCurrentUser();
    async function getCurrentUser() {
      let theCurrentUserId = localStorage.getItem("storeCurrentUser");
      if (theCurrentUserId) {
        let fetchedUser = await httpService
          .get(`/users?search=${theCurrentUserId}`)
          .then((response) => response.data);

        setSignedIn({
          id: fetchedUser[0].id,
          userId: fetchedUser[0].userId,
          name: fetchedUser[0].name,
          email: fetchedUser[0].email,
          password: fetchedUser[0].password,
          level: fetchedUser[0].level,
        });
      } else {
        setSignedIn({});
      }
    }
  }, []);

  //Reset Modal Error when changing Modal Form Type
  useEffect(() => {
    setModalFormError("");
    if (modalFormType !== "editProduct") {
      setProductToEdit("");
    }
  }, [modalFormType]);

  //Get and Set User Cart
  useEffect(() => {
    if (signedIn !== {} && signedIn.id !== undefined) {
      getAndSetUserCart();
    }

    async function getAndSetUserCart() {
      let fetchedUserCart = await httpService
        .get(`/users/${signedIn.id}/cart/${signedIn.id}`)
        .then((response) => response.data);

      let cartFromProducts = { ...fetchedUserCart, cartProducts: [] };
      for (const productInCart of fetchedUserCart.cartProducts) {
        for (const productInStore of products) {
          if (Number(productInCart[0].sn) === Number(productInStore.sn)) {
            cartFromProducts.cartProducts = [
              ...cartFromProducts.cartProducts,
              [productInStore, productInCart[1]],
            ];
          }
        }
      }

      setCart(cartFromProducts);
    }
  }, [signedIn, products]);

  //Save Cart
  useEffect(() => {
    if (signedIn !== {} && signedIn.id) {
      saveCart();
    }

    async function saveCart() {
      await httpService
        .put(`/users/${signedIn.id}/cart/${signedIn.id}`, {
          "cartProducts": cart.cartProducts,
        })
        .then((response) => response.data);
    }
  }, [cart, signedIn]);

  //Handles Sign In/Up
  async function handleSignedIn(user = null) {
    await httpService
      .get(`/users?search=${user.email}`)
      .then((response) => response.data)
      .then((userData) => {
        if (modalFormType === "signIn") {
          if (userData[0]?.email === user.email) {
            if (userData[0]?.password === user.password) {
              localStorage.setItem("storeCurrentUser", userData[0].userId);
              setSignedIn(userData[0]);
              setModalFormType(null);
            } else {
              setModalFormError("Sorry. Wrong Password..");
            }
          } else {
            setModalFormError("Sorry. User with this Email Doesn't Exist..");
          }
        } else if (modalFormType === "signUp") {
          if (userData.length !== 0) {
            setModalFormError("EMAIL already EXISTS!");
          } else {
            createNewUser(user);
            setModalFormType(null);
          }
        }
      });
  }

  async function createNewUser(user) {
    let newUser = await httpService.post("/users", {
      "name": user.name,
      "email": user.email,
      "password": user.password,
      "level": user.level,
    });
    newUser = await newUser.data;

    await httpService.post(`/users/${newUser.id}/cart`, {
      "userId": newUser.id,
    });

    localStorage.setItem("storeCurrentUser", newUser.userId);
    setSignedIn({ ...newUser });
    setModalFormError("");
    setModalFormType(null);
  }

  function handleSignOut() {
    localStorage.removeItem("storeCurrentUser");
    setSignedIn({});
    setCart({});
  }

  function handleAmountChange(newAmount, sn) {
    const newCartProducts = cart.cartProducts.map((product) => {
      if (product[0].sn === sn) {
        return [product[0], newAmount];
      } else {
        return product;
      }
    });
    setCart({ ...cart, cartProducts: newCartProducts });
  }

  function removeFromCart(sn) {
    const newCartProducts = cart.cartProducts.filter(
      (product) => product[0].sn !== sn
    );
    setCart({ ...cart, cartProducts: newCartProducts });
  }

  function addToCart(product, amount) {
    const isInCart = cart.cartProducts.filter(
      (productInCart) => productInCart[0].sn === product.sn
    );
    if (isInCart.length === 0) {
      const newCartProducts = [...cart.cartProducts, [product, amount]];
      setCart({ ...cart, cartProducts: newCartProducts });
    } else {
      handleAmountChange(amount, product.sn);
    }
  }

  //handle product changes (new/edit/delete)
  function handleNewProduct(newProduct) {
    const searchForProduct = products.filter(
      (product) => Number(product.sn) === Number(newProduct.productSN)
    );
    if (searchForProduct.length === 0) {
      addNewProduct(newProduct);
    } else {
      setModalFormError(
        "Serial Number must be UNIQUE. A product with this Serial Number already exists.."
      );
    }
  }
  async function addNewProduct(product) {
    await httpService
      .post("/products", {
        "sn": product.productSN,
        "name": product.productName,
        "description": product.productDescription,
        "price": product.productPrice,
        "unit": product.productUnit,
        "image": product.productImage,
        "category": product.productCategory,
      })
      .then((response) => response.data);
    setProducts([
      ...products,
      {
        "sn": product.productSN,
        "name": product.productName,
        "description": product.productDescription,
        "price": product.productPrice,
        "unit": product.productUnit,
        "image": product.productImage,
        "category": product.productCategory,
      },
    ]);
    setModalFormType(null);
  }
  function handleEditProduct(editedProduct) {
    const searchForProduct = products.filter(
      (product) => Number(product.sn) === Number(editedProduct.productSN)
    );
    if (searchForProduct.length !== 0) {
      editProduct(editedProduct, searchForProduct[0].id);
    } else {
      setModalFormError("Product Not Found..");
    }
  }
  async function editProduct(editedProduct, editedProductID) {
    await httpService
      .put(`/products/${editedProductID}`, {
        "name": editedProduct.productName,
        "description": editedProduct.productDescription,
        "price": editedProduct.productPrice,
        "unit": editedProduct.productUnit,
        "image": editedProduct.productImage,
        "category": editedProduct.productCategory,
      })
      .then((response) => response.data);
    const editedProducts = products.map((product) => {
      if (product.id === editedProductID) {
        return {
          "id": product.id,
          "sn": product.sn,
          "name": editedProduct.productName,
          "description": editedProduct.productDescription,
          "price": editedProduct.productPrice,
          "unit": editedProduct.productUnit,
          "image": editedProduct.productImage,
          "category": editedProduct.productCategory,
        };
      } else {
        return product;
      }
    });
    setProducts(editedProducts);
    setModalFormType(null);
    setModalFormError("");
  }
  async function handleDeleteProduct(productToDelete) {
    await httpService
      .delete(`/products/${productToDelete.id}`)
      .then((response) => response.data);
    const productsAfterDelete = products.filter(
      (product) => product.id !== productToDelete.id
    );
    setProducts(productsAfterDelete);
    setModalFormType(null);
  }

  return (
    <storeContext.Provider
      value={{
        signedIn,
        products,
        productsFilter,
        setProductsFilter,
        cart,
        modalFormType,
        setModalFormType,
        modalFormError,
        productToEdit,
        setProductToEdit,
        handleSignedIn,
        handleSignOut,
        handleAmountChange,
        removeFromCart,
        addToCart,
        handleNewProduct,
        handleEditProduct,
        handleDeleteProduct,
      }}
    >
      {children}
    </storeContext.Provider>
  );
}

export const useStoreContext = () => {
  return useContext(storeContext);
};
