import { useState } from "react";
import { useStoreContext } from "../context/store.context";

export function Product({
  product: { sn, name, category, image, price, unit, id, description },
  fullProduct,
}) {
  const {
    signedIn,
    cart,
    handleAmountChange,
    addToCart,
    setProductToEdit,
    setModalFormType,
  } = useStoreContext();
  // const [inCart, setInCart] = useState(false);
  const [productAmount, setProductAmount] = useState(getAmount);

  const signedInLevel = signedIn?.level;

  const inCart = isInCart();
  function isInCart() {
    const cartCheck = cart.cartProducts?.filter(
      (product) => product[0].sn === sn
    );
    if (cartCheck && cartCheck.length !== 0) {
      return true;
    } else {
      return false;
    }
  }

  function getAmount() {
    const amountFromCart = cart.cartProducts?.filter(
      (product) => product[0].sn === sn
    );
    if (amountFromCart && amountFromCart.length !== 0) {
      return amountFromCart[0][1];
    } else {
      return 0;
    }
  }

  function setEditProduct() {
    setModalFormType("editProduct");
    setProductToEdit(fullProduct);
  }

  return (
    <div
      id={`product${sn}`}
      className={`productDiv ${category}`}
      style={signedInLevel === "admin" ? { paddingTop: "35px" } : {}}
    >
      <h3 className="productTitle">{name}</h3>
      <img className="productImg" src={image} alt={name} />
      <p className="productDesc">{description}</p>
      <p className="productPrice">
        Price: {price}$ for 1 {unit}
      </p>
      <p className="productAmount">
        Amount:{" "}
        <input
          type="number"
          id={`productAmountInput${sn}`}
          className="productAmountInput"
          min="0"
          max="5"
          step={unit === "KG" ? "0.5" : "1"}
          placeholder="0"
          value={inCart ? getAmount() : productAmount}
          onChange={
            inCart
              ? (e) => handleAmountChange(e.target.value, sn)
              : (e) => setProductAmount(e.target.value)
          }
        />
        {" " + unit}
      </p>
      <p className="addProductP">
        <input
          disabled={inCart || signedInLevel === "none"}
          type="submit"
          id={`addProduct${sn}`}
          className="addProduct"
          value="Add to Cart"
          onClick={() => addToCart(fullProduct, productAmount)}
        />
      </p>
      {signedInLevel === "admin" && (
        <button className="btn editBTN" onClick={setEditProduct}>
          <i className="bi bi-pencil-square"></i> Edit Product
        </button>
      )}
      {signedInLevel === "none" && (
        <p className="userProductAlert text-danger text-center mb-0 fw-bold">
          Please Sign In/Up to Buy this Product..
        </p>
      )}
    </div>
  );
}
