import { useStoreContext } from "../context/store.context";
import { CartProduct } from "./cartProduct";

export function Cart() {
  const { cart, handleAmountChange, removeFromCart } = useStoreContext();

  let total = 0;
  cart.cartProducts?.forEach((cartProduct) => {
    total += Number(cartProduct[0].price) * Number(cartProduct[1]);
  });

  return (
    <div id="cartDiv">
      <h3>Your Cart</h3>
      <div id="cartProducts">
        {cart.cartProducts?.map((cartProduct) => (
          <CartProduct
            key={cartProduct[0].sn}
            cartProduct={cartProduct}
            cart={cart}
            handleAmountChange={handleAmountChange}
            removeFromCart={removeFromCart}
          />
        ))}
      </div>
      <div id="cartTotal">
        <span id="cartTotalSumTitle">Total: </span>
        <span id="cartTotalSum">
          <span id="cartTotalSumNum">{total}</span>$
        </span>
      </div>
    </div>
  );
}
