import { useStoreContext } from "../context/store.context";

export function CartProduct({
  cartProduct: [{ sn, name, price, unit, image }, amount],
}) {
  const { handleAmountChange, removeFromCart } = useStoreContext();
  //   const [productAmount, setProductAmount] = useState(amount);

  //   function handleAmountChange(newAmount) {
  //     setProductAmount(newAmount);
  //     const newCartProducts = cart.cartProducts.map((product) => {
  //       if (product[0].sn === sn) {
  //         return [product[0], newAmount];
  //       } else {
  //         return product;
  //       }
  //     });
  //     setCart({ ...cart, cartProducts: newCartProducts });
  //   }

  return (
    <div className="cartProduct" id={`cartProduct${sn}`}>
      <img className="cartProductImg" src={image} alt={name} />
      <h4 className="cartProductTitle">
        <span>{name}:</span>
      </h4>
      <span className="cartProductPrice">{price}$ *</span>
      <input
        className="cartProductAmount form-control"
        id={`cartProductAmount${sn}`}
        type="number"
        min="0"
        max="5"
        value={amount}
        step={unit === "Units" ? "1" : "0.5"}
        onChange={(e) => handleAmountChange(e.target.value, sn)}
      />
      <span>{unit}</span>=
      <span className="cartProductTotalPrice" id={`cartProductTotalPrice${sn}`}>
        {price * amount}$
      </span>
      <button
        onClick={() => removeFromCart(sn)}
        className="cartProductDelete btn"
        id={`cartProductDelete${sn}`}
      >
        X
      </button>
    </div>
  );
}
