import { useStoreContext } from "../context/store.context";

export function SignOut() {
  const { signedIn, handleSignOut } = useStoreContext();
  return (
    <>
      <span id="theUserName">{signedIn.name}</span>
      <button
        onClick={() => {
          handleSignOut();
        }}
        className="btn text-decoration-underline"
        id="userSignOut"
      >
        (Sign Out)
      </button>
    </>
  );
}
